<template>
  <div class="submit">
    <div class="title">您的佳作</div>
    <img class="pic" v-bind:style="{ borderColor: $route.params.color }" v-bind:src="$route.params.img"></img>
    <img id="tips" src="static/submit_tips.svg" />
    <div id="tips2" >请留下您的联系方式，方便工作人员与您联系。</div>
    <input type="number" id="phone" placeholder="手机号" v-bind:style="{ borderColor: $route.params.color }"></input>
    <button v-on:click="submit">
      <img id="submit" src="static/submit.svg" />
    </button>
  </div>
</template>

<script>
import AV from 'leancloud-storage'

export default {
  name: 'submit',
  data () {
    return {

    }
  },

  computed: {
    phone () {
      return document.getElementById('phone').value
    },
    author () {
      return ''
    }
  },

  beforeCreate () {
    if (!this.$route.params.img) {
      this.$router.replace('/')
    }
  },

  methods: {
    submit () {
      if (this.phone.length === 0) {
        alert('请输入联系方式')
        return
      }

      let b64data = this.$route.params.img.replace(/^data:image\/(png|jpg);base64,/, '')
      let data = {base64: b64data}
      let file = new AV.File('canvas.png', data)
      file.metaData('author', this.author)
      file.metaData('phone', this.phone)

      let btn = document.getElementById('submit')
      btn.disabled = true
      file.save({
        onprogress: (e) => {
          if (e.direction === 'upload') { return }
          console.log('progress: ' + e.progress)
        }
      }, {}).then((file) => {
        console.log(file)
        alert('上传成功！')
        // this.queryCounter()
        this.$router.push({
          path: 'feedback',
          query: {
            'objectId': file.id
          }
        })
      }, (error) => {
        console.error(error)
        alert('上传失败！')
        btn.disabled = false
      })
    },

    queryCounter () {
      var query = new AV.Query('_File')
      query.count().then((count) => {
        alert('count: ' + count)
      }, (error) => {
        console.log(error)
      })
    }
  },

  watch: {

  }
}
</script>

<style scoped>
.submit {
  /*width: 90vw;*/
  /*margin: 0 auto;*/
}
.title {
  font-size: 5vw;
  text-align: center;
  padding: 3vw;
}
.pic {
  height: 80vw;
  width: 90vw;
  /*object-fit: cover;*/
  object-fit: contain;

  border: 0.6vw solid;
  border-radius: 5vw;
  /*border-color: #ff5800;*/
}
#tips {
  width: 50vw;
  /*height: 10vw;*/
  height: auto;
  display: block;
  margin: 6vw;
}
#tips2 {
  font-size: 3vw;
  margin: 3vw;
}
#phone {
  width: 75vw;
  padding: 2vw;
  font-size: 4vw;

  border: 0.6vw solid;
  border-radius: 5vw;
  /*border-color: #ff5800;*/
  padding-left: 3vw;
}
#submit {
  width: 45vw;
  height: auto;
  margin: 8vw;
}
</style>



